  top: 0;
  position: sticky;
}


.navbar__logo__content::before {
  position: absolute;
  content: "";
  background-color: #ffeb38;
  width: 200px;
  height: 30px;
  top: 0;

  border-radius: 0px 50px 0px 0px;
  margin-top: 2em;
