.side_nav {
  width: 250px;
  height: 100vh;
  position: absolute;
  right: 0;
  top: 0;
  
  background: #ffeb38;
  z-index: 3;

  display: none;
  animation: slider 1s both;
  transition: 300ms ease-in-out;
}
.side_nav ul li {
  list-style: none;
  margin: 50px 20px;
}
.side_nav ul li a {
  text-decoration: none;
  color: black;
  font-size: 18px;

}
.side_nav ul li a:hover {
  text-decoration: revert;
  color: black;
  font-weight: bold;

}

@keyframes slider {
  from {
    right: -250px;
  }
  to {
    right: 0px;
  }
}
.menu_btn {
  width: 50px;
